<template>
        <div class="pos">
            <el-row>
                <el-col :span='7' class="pos-order" id="order-list">
                  <el-tabs>
                    <el-tab-pane label="点餐">
                        <el-table :data="tableData">
                          <el-table-column prop="goodsName" label="商品名称"></el-table-column>
                          <el-table-column prop="count" label="数量"></el-table-column>
                          <el-table-column prop="price" label="金额"></el-table-column>
                          <el-table-column  label="操作" fixed="right" width="100">
                              <template scope="scope">
                                  <el-button type="text" size="small" @click="deleteGoods(scope.row)">删除</el-button>
                                  <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
                              </template>
                          </el-table-column>
                        </el-table>
                        <div class="totalDiv">
                          <small>数量：</small>{{ totalCount }} &nbsp; <small>金额：</small>{{ totalMoney }}元
                        </div>
                        <div class="btn">
                            <el-button type="danger" @click="deleteAllGoods()">删除</el-button>
                            <el-button type="success" @click="checkout()">结账</el-button>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="挂单">
                        挂单
                    </el-tab-pane>
                    <el-tab-pane label="外卖">
                        外卖
                    </el-tab-pane>
                  </el-tabs>
                </el-col>
                <el-col :span='17'>
                  <div class="often-goods">
                      <div class="title">常用商品</div>
                      <div class="often-goods-list">
                        <ul>
                          <li v-for="item in oftenGoods" @click="addOrderList(item)">
                            <span class="o-name">{{ item.goodsName }}</span>
                            <span class="o-price">{{ item.price }}</span>
                          </li>
                        </ul>
                      </div>
                  </div>
                  <div class="goods-type">
                    <el-tabs>
                      <el-tab-pane label="汉堡">
                        <ul class="cook-list">
                          <li v-for="goods in type0Goods" @click="addOrderList(goods)">
                            <span class="food-img"><img :src="goods.goodsImg" alt=""></span>
                            <span class="food-name">{{ goods.goodsName }}</span>
                            <span class="food-price">￥{{ goods.price }}</span>
                          </li>
                        </ul>
                      </el-tab-pane>
                      <el-tab-pane label="小食">
                          <ul class="cook-list">
                              <li v-for="goods in type1Goods" @click="addOrderList(goods)">
                                <span class="food-img"><img :src="goods.goodsImg" alt=""></span>
                                <span class="food-name">{{ goods.goodsName }}</span>
                                <span class="food-price">￥{{ goods.price }}</span>
                              </li>
                            </ul>
                      </el-tab-pane>
                      <el-tab-pane label="饮料">
                          <ul class="cook-list">
                              <li v-for="goods in type2Goods" @click="addOrderList(goods)">
                                <span class="food-img"><img :src="goods.goodsImg" alt=""></span>
                                <span class="food-name">{{ goods.goodsName }}</span>
                                <span class="food-price">￥{{ goods.price }}</span>
                              </li>
                            </ul>
                      </el-tab-pane>
                      <el-tab-pane label="套餐">
                          <ul class="cook-list">
                              <li v-for="goods in type3Goods" @click="addOrderList(goods)">
                                <span class="food-img"><img :src="goods.goodsImg" alt=""></span>
                                <span class="food-name">{{ goods.goodsName }}</span>
                                <span class="food-price">￥{{ goods.price }}</span>
                              </li>
                            </ul>
                      </el-tab-pane>
                    </el-tabs>
                  </div>
                </el-col>
            </el-row>
        </div>
      </template>
      
      <script>
      import axios from 'axios';
      export default {
        name: 'pos',
        data(){
          return {
            tableData: [],
            oftenGoods:[],
            type0Goods:[],
            type1Goods:[],
            type2Goods:[],
            type3Goods:[],
            totalMoney:0,
            totalCount:0
            }
        },
        created:function(){
          axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/oftenGoods')
          .then(response=>{
            this.oftenGoods = response.data
          })
          .catch(error=>{
            //console.log(error)
          })

          axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/typeGoods')
          .then(response=>{
            this.type0Goods = response.data[0]
            this.type1Goods = response.data[1]
            this.type2Goods = response.data[2]
            this.type3Goods = response.data[3]
          })
          .catch(error=>{
            //console.log(error)
          })
          
        },
        mounted:function(){
          var orderHeight = document.body.clientHeight;
          document.querySelector('#order-list').style.height = orderHeight+'px'
        },
        methods:{
          addOrderList(goods){
            this.totalCount = 0
            this.totalMoney = 0
            //商品是否存在于订单列表中
            let isHava = false
            for(let i = 0 ;i< this.tableData.length;i++){
              if(this.tableData[i].goodsId === goods.goodsId){
                isHava = true
              }
            }
            //根据判断的值编写业务逻辑
            if(isHava === true){
              //改变列表中商品的数量
              let arr = this.tableData.filter(a=>a.goodsId == goods.goodsId)
              arr[0].count++
            }else{
              let newGoods = {goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1}
              this.tableData.push(newGoods)
            }
            this.getAllMoney()
          },
          deleteGoods(goods){
            this.tableData = this.tableData.filter(o=>o.goodsId != goods.goodsId)
            this.getAllMoney()
          },
          deleteAllGoods(){
            this.tableData = []
            this.totalCount = 0
            this.totalMoney = 0
          },
          //汇总数量和金额
          getAllMoney(){
            this.totalCount = 0
            this.totalMoney = 0
            this.tableData.forEach((element)=>{
              this.totalCount += element.count
              this.totalMoney = this.totalMoney+element.price*element.count
            })
          },
          //模拟结账
          checkout(){
            if(this.tableData != false){
              this.tableData = []
              this.totalCount = 0
              this.totalMoney = 0
              this.$message({
                message:'结账成功',
                type:'success'
              })
            }else{
              this.$message({
                message:'商品为空，不能进行操作',
                type:'error'
              })
            }
          }
        }
      }
      </script>
      
      <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .pos-order {
    background: #F9FAFC;
    border-right: 1px solid #C0CCDA;
    height: 100%;
  }

  .btn {
    margin-top: 30px;
  }

  .title {
    height: 20px;
    border-bottom: 1px solid #D3dce6;
    background-color: #F9FAFC;
    padding:10px;
    text-align: left;
  }

  .often-goods-list ul li{
    list-style: none;
    float: left;
    border: 1px solid #F9FAFC;
    padding: 10xp;
    margin: 10px;
  }

  .o-price {
    color: #58B7FF
  }

  .goods-type {
    clear: both;
  }

  .cook-list li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
   }
   .cook-list li span{
        display: block;
        float:left;
   }
   .food-img{
       width: 40%;
   }
   .food-name{
       font-size: 18px;
       padding-left: 10px;
       color:brown;
   }
   .food-price{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }

   .totalDiv{
    background-color: #fff;
    padding: 10px;
    border-bottom: 1px solid #ccc
   }  

</style>
      


